const API_BASE_URL = 'http://127.0.0.1:8002/api';

export interface User {
    id: number;
    name: string;
    email: string;
    created_at: string;
    updated_at: string;
}

export interface LoginRequest {
    email: string;
    password: string;
}

export interface RegisterRequest {
    name: string;
    email: string;
    password: string;
}

export interface AuthResponse {
    user: User;
    token: string;
}

class AuthService {
    private async request<T>(
        endpoint: string,
        options: RequestInit = {}
    ): Promise<T> {
        const url = `${API_BASE_URL}${endpoint}`;
        
        const config: RequestInit = {
            headers: {
                'Content-Type': 'application/json',
                ...options.headers,
            },
            ...options,
        };

        // Add auth token if available
        const token = this.getToken();
        if (token) {
            config.headers = {
                ...config.headers,
                'Authorization': `Bearer ${token}`,
            };
        }

        try {
            const response = await fetch(url, config);
            
            if (!response.ok) {
                const errorText = await response.text();
                let errorMessage;
                try {
                    const errorJson = JSON.parse(errorText);
                    errorMessage = errorJson.message || errorJson.errors?.email?.[0] || errorJson.errors?.password?.[0] || `API Error: ${response.status}`;
                } catch {
                    errorMessage = `API Error: ${response.status} - ${errorText}`;
                }
                throw new Error(errorMessage);
            }
            
            return await response.json();
        } catch (error) {
            console.error('Auth API Request failed:', error);
            throw error;
        }
    }

    // Register new user
    async register(userData: RegisterRequest): Promise<AuthResponse> {
        return this.request<AuthResponse>('/register', {
            method: 'POST',
            body: JSON.stringify(userData),
        });
    }

    // Login user
    async login(credentials: LoginRequest): Promise<AuthResponse> {
        return this.request<AuthResponse>('/login', {
            method: 'POST',
            body: JSON.stringify(credentials),
        });
    }

    // Get current user
    async getCurrentUser(): Promise<{ user: User }> {
        return this.request<{ user: User }>('/user');
    }

    // Logout user
    async logout(): Promise<{ message: string }> {
        const response = await this.request<{ message: string }>('/logout', {
            method: 'POST',
        });
        this.clearToken();
        this.clearUser();
        return response;
    }

    // Token management
    getToken(): string | null {
        if (typeof window === 'undefined') return null;
        return localStorage.getItem('auth_token');
    }

    setToken(token: string): void {
        if (typeof window === 'undefined') return;
        localStorage.setItem('auth_token', token);
    }

    clearToken(): void {
        if (typeof window === 'undefined') return;
        localStorage.removeItem('auth_token');
    }

    // User management
    getUserFromStorage(): User | null {
        if (typeof window === 'undefined') return null;
        const userStr = localStorage.getItem('auth_user');
        if (!userStr) return null;
        try {
            return JSON.parse(userStr);
        } catch {
            return null;
        }
    }

    setUser(user: User): void {
        if (typeof window === 'undefined') return;
        localStorage.setItem('auth_user', JSON.stringify(user));
    }

    clearUser(): void {
        if (typeof window === 'undefined') return;
        localStorage.removeItem('auth_user');
    }

    // Check if user is authenticated
    isAuthenticated(): boolean {
        return !!(this.getToken() && this.getUserFromStorage());
    }

    // Initialize auth state from storage
    initializeAuth(): { user: User | null; token: string | null } {
        const token = this.getToken();
        const user = this.getUserFromStorage();
        
        if (token && user) {
            return { user, token };
        }
        
        // Clear invalid data
        this.clearToken();
        this.clearUser();
        
        return { user: null, token: null };
    }
}

export const authService = new AuthService();
