"use client";

import { useState, useEffect, createContext, useContext, ReactNode } from 'react';
import { authService, User, LoginRequest, RegisterRequest } from '@/lib/auth';

interface AuthContextType {
    user: User | null;
    token: string | null;
    login: (credentials: LoginRequest) => Promise<void>;
    register: (userData: RegisterRequest) => Promise<void>;
    logout: () => void;
    loading: boolean;
    error: string | null;
    isAuthenticated: boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export { AuthContext };

export function useAuth() {
    const context = useContext(AuthContext);
    if (context === undefined) {
        throw new Error('useAuth must be used within an AuthProvider');
    }
    return context;
}

interface AuthProviderProps {
    children: ReactNode;
}

export function AuthProvider({ children }: AuthProviderProps) {
    const [user, setUser] = useState<User | null>(null);
    const [token, setToken] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        // –ü—Ä–∏ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏ –ø—Ä–æ–≤–µ—Ä—è–µ–º –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—é
        const authData = authService.initializeAuth();
        setUser(authData.user);
        setToken(authData.token);
    }, []);

    const login = async (credentials: LoginRequest) => {
        setLoading(true);
        setError(null);
        
        try {
            const response = await authService.login(credentials);
            const user = response.user || null;
            const token = response.token || null;
            
            setUser(user);
            setToken(token);
            
            // Store in localStorage
            if (token) authService.setToken(token);
            if (user) authService.setUser(user);
        } catch (err) {
            const errorMessage = err instanceof Error ? err.message : 'Login failed';
            setError(errorMessage);
            throw err;
        } finally {
            setLoading(false);
        }
    };

    const register = async (userData: RegisterRequest) => {
        setLoading(true);
        setError(null);
        
        try {
            const response = await authService.register(userData);
            const user = response.user || null;
            const token = response.token || null;
            
            setUser(user);
            setToken(token);
            
            // Store in localStorage
            if (token) authService.setToken(token);
            if (user) authService.setUser(user);
        } catch (err) {
            const errorMessage = err instanceof Error ? err.message : 'Registration failed';
            setError(errorMessage);
            throw err;
        } finally {
            setLoading(false);
        }
    };

    const logout = async () => {
        try {
            await authService.logout();
        } catch (err) {
            console.error('Logout error:', err);
        } finally {
            setUser(null);
            setToken(null);
            setError(null);
        }
    };

    const value: AuthContextType = {
        user,
        token,
        login,
        register,
        logout,
        loading,
        error,
        isAuthenticated: !!(token && user)
    };

    return (
        <AuthContext.Provider value={value}>
            {children}
        </AuthContext.Provider>
    );
}
